.header {
  width: 100%;
  height: 4rem;
  padding: 0 10%;
  top: 0;
  z-index: 1;
  position: fixed;
  background-color: #2a9d8f;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.actions {
  width: 30%;
  display: flex;
}

.actions .button {
  font-size: large;
  width: 40%;
  height: 2.5em;
}

.actions .button span {
  color: white;
  padding: 0 0.2em;
}

.mainImg {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.mainImg img {
  width: 110%;
  height: 100%;
  background: center no-repeat;
  transform: rotateZ(5deg) translateY(-4rem) translateX(-1rem);
}

@media screen and (max-width: 1080px) {
  .hide {
    display: none;
  }
}

@media screen and (max-width: 660px) {
  .header {
    padding: 0 5%;
  }

  .actions {
    width: 40%;
  }
}

@media screen and (max-width: 425px) {
  .header h1 {
    font-size: 1.7rem;
  }
}
